.object {

    &__body {
        @media (max-width:$desktop) {
            padding: 0;
            background-color: var(--stand-bg);
            border-radius: 0 0 12px 12px;
        }
    }

    @media (max-width:$desktop) {
        padding-top: 0;
    }
}

.object-body {
    &__slider {
        padding: 10px;
        background-color: var(--white);
        box-shadow: var(--shadow-primary);
        border-radius: 12px;

        @media (max-width:$desktop) {
            padding: 0;
            background-color: transparent;
        }
    }

    &__container {
        margin-top: 8px;
        padding: 32px;
        background-color: var(--stand-bg);
        border-radius: 16px;

        @media (max-width:$desktop) {
            margin-top: 0;
            padding: 62px 0 16px;
            background-color: transparent;
        }
    }

    &__wrapper {
        margin-top: 16px;

        display: grid;
        justify-content: space-between;
        grid-template-columns: 1fr 300px;
        grid-column-gap: 16px;

        @media (max-width:$desktop) {
            margin-top: 8px;
            grid-template-columns: 1fr;
        }
    }

    &__sidebar {
        @media (max-width:$desktop) {
            margin-top: 8px;
            order: 1;
        }
    }

    &__purchase-terms {
        margin-top: 24px;
    }

    &__content {
        min-width: 0;

        @media (max-width:$desktop) {
            order: 2;
        }
    }

    &__comment {}

    &__user {}

    &__data {
        margin-top: 16px;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__banners {
        margin-top: 16px;
        min-width: 100%;
        width: 100%;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__history {
        margin-top: 16px;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__location {
        margin-top: 16px;
        min-width: 100%;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__characteristics {
        margin-top: 16px;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__calc-mort {
        margin-top: 16px;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__similar-objects {
        margin-top: 25px;

        @media (max-width:$smallTablet-max) {
            margin-top: 8px;
        }
    }

    &__stock {
        margin-top: 25px;
        background-color: var(--white);
        box-shadow: var(--shadow-primary);
        border-radius: 12px;
        padding: 32px 24px 24px;

        @media (max-width:$desktop) {
            padding: 16px;
            margin-top: 8px;
        }
    }

    &__filter {
        margin-top: 24px;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__col {
        margin-top: 24px;
        display: grid;
        grid-template-columns: percent(700, 1016);
        grid-row-gap: 24px;

        @media (max-width:$desktop) {
            margin-top: 16px;
            grid-template-columns: 100%;
            grid-row-gap: 16px;
        }
    }

    &__descr {
        .object-data__text {
            margin-top: 24px;
        }
    }

    &__other-develop {
        margin-top: 24px;

        @media (max-width:$desktop) {
            margin-top: 0;
        }
    }

    &__similar-build {
        margin-top: 24px;

        @media (max-width:$desktop) {
            margin-top: 16px;
        }
    }
}

.object-other-apartment {
    box-shadow: var(--shadow-primary);
    background-color: var(--white);
    border-radius: 12px;
    padding: 32px 24px;

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 24px;
    }

    &__title {}

    &__nav {
        display: flex;
        align-items: center;

        .nav-arrow-secondary {
            &:not(:last-child) {
                margin-right: 10px;
            }
        }

        @media (max-width:$smallTablet-max) {
            display: none;
        }
    }

    &__items {

        .swiper-wrapper {
            align-items: stretch;
        }

        .swiper-slide {
            height: auto;
        }

        .card-primary {
            height: 100%;
        }

        @media (max-width:$desktop) {
            margin: -16px;
            padding: 16px;
        }
    }

    @media (max-width:$desktop) {
        padding: 16px;
    }
}

.object-advantages {
    box-shadow: var(--shadow-primary);
    background-color: var(--white);
    padding: 32px 24px 24px;
    border-radius: 12px;

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 24px;

        @media (max-width:$desktop) {
            margin-bottom: 16px;
        }
    }

    &__nav {
        display: flex;
        align-items: center;

        .nav-arrow-secondary--next {
            margin-left: 10px;
        }

        @media (max-width:$smallTablet-max) {
            display: none;
        }
    }

    &__title {}

    &__slider {
        @media (max-width:$desktop) {
            padding: 0 16px;
            margin: 0 -16px;
        }
    }

    &__items {}

    &__btn {
        margin-top: 24px;
        width: 100%;

        &._active {
            position: relative;
            font-size: 0;

            &::before {
                content: 'Свернуть';
                font-size: 14px;
            }
        }
    }

    .advantages-card {
        width: 100%;

        &__container {
            width: 100%;
        }
    }

    &._active {
        .object-advantages {
            &__slider {
                overflow: visible;
            }

            &__nav {
                display: none;
            }

            &__items {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                grid-column-gap: 16px;
                grid-row-gap: 32px;

                @media (max-width:$mobile-max) {
                    grid-template-columns: 1fr;
                }
            }
        }
    }

    @media (max-width:$desktop) {
        padding: 16px 16px 24px;
    }
}

.object-filter {
    box-shadow: var(--shadow-primary);
    background-color: var(--white);
    padding: 32px 24px;
    border-radius: 16px;

    &__top {
        margin-bottom: 32px;
        display: flex;
        align-items: end;
        justify-content: space-between;

        @media (max-width:$desktop) {
            margin-bottom: 16px;
            flex-direction: column;
            align-items: start;
            justify-content: start;
            padding: 0 16px;
        }
    }

    &__date {
        color: var(--primaryTwo);
        font-size: 12px;
        line-height: math.div(15, 12);
        font-weight: 500;

        @media (max-width:$desktop) {
            margin-top: 6px;
            font-weight: 400;
        }
    }

    .filter__btn {
        @media (max-width:$desktop) {
            margin: 0 16px;
            width: calc(100% - 32px);
        }
    }

    &__row {
        &--1 {
            grid-template-columns: percent(130, 968) percent(255, 968) percent(275, 968) percent(260, 968);
        }

        &--2 {
            grid-template-columns: percent(175, 968) max-content;
            justify-content: start;

            .filter__item--checkboxes {
                margin-left: 8px;
                display: flex;
                align-items: center;

                >.checkbox-secondary {
                    &:not(:last-child) {
                        margin-right: 20px;

                        @media (max-width:$desktop) {
                            margin-right: 0;
                            margin-bottom: 14px;
                        }
                    }
                }

                @media (max-width:$desktop) {
                    margin: 0;
                    align-self: start;
                    flex-direction: column;
                }
            }
        }
    }

    &__sort {
        .choices__list--dropdown {
            width: max-content;
        }

        @media (max-width:$desktop) {
            padding: 0 $containerOffset+px;
            margin-top: 16px;
            justify-content: center;
        }
    }

    &__tabs {
        margin-top: 40px;

        .tabs-primary__inner {
            margin-top: 0;

            @media (max-width:$desktop) {
                padding-top: 0;
            }
        }

        .tabs-primary__container {
            padding: 0;
            margin-top: 32px;
            background-color: var(--white);

            @media (max-width:$desktop) {
                margin-top: 16px;
            }
        }

        .tabs-primary__content {
            margin-top: 0;
        }

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__checkboard {}

    &__layouts {}

    @media (max-width:$desktop) {
        padding: 16px 0 0;
    }
}

.object-characteristics {
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    border-radius: 12px;
    padding: 32px 24px 24px;

    &__title {}

    &__list {
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 110px;
        grid-row-gap: 16px;

        &:first-child {
            margin-top: 24px;
        }

        &:not(:first-child) {
            margin-top: 16px;
        }

        &:not([hidden]) {
            display: grid;
        }

        @media (max-width:$smallTablet-max) {
            grid-template-columns: 1fr;
            grid-row-gap: 8px;

            &:first-child {
                margin-top: 16px;
            }

            &:not(:first-child) {
                margin-top: 8px;
            }
        }
    }

    &__item {
        display: flex;
        align-items: center;
        width: 100%;

        &::after {
            content: '';
            flex: 1 1 0%;
            align-self: end;
            position: relative;
            top: -3px;
            border-bottom: 1px dashed var(--primary200);
            height: 1px;
            order: 2;
            margin: 0 2px;
        }

        span {
            font-size: 15px;
            line-height: math.div(19, 15);
            font-weight: 400;

            &:nth-child(1) {
                order: 1;
                color: var(--primaryTwo);

                @media (max-width:$smallTablet-max) {
                    width: 50%;
                }
            }

            &:nth-child(2) {
                order: 3;

                @media (max-width:$smallTablet-max) {
                    order: 2;
                    width: 42%;
                }
            }
        }

        @media (max-width:$smallTablet-max) {
            justify-content: space-between;

            &::after {
                display: none;
            }
        }
    }

    &__more {
        display: flex;
        align-items: center;
        margin-top: 16px;
        color: var(--blue);
        font-size: 12px;
        line-height: 100%;

        svg {
            margin-left: 5px;
            width: 20px;
            height: 20px;
            fill: currentColor;
            transition: transform $transitionSpeedFast $transitionFunction;
        }

        &._active {
            svg {
                transform: rotate(-180deg);
            }
        }

        @media (max-width:$tablet-max) {
            margin-top: 8px;
        }
    }

    @media (max-width:$desktop) {
        padding: 16px;
    }
}

// ================================================================================
.object-comment {
    border-radius: 12px;
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    padding: 32px 24px;

    &__title {}

    &__descr {
        margin-top: 24px;
        font-size: 15px;
        line-height: math.div(19, 15);
        color: var(--dark);

        @media (max-width:$desktop) {
            margin-top: 16px;
        }
    }

    @media (max-width:$desktop) {
        padding: 16px 16px 24px;
    }
}

.object-data {
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    padding: 32px 24px 24px;
    border-radius: 12px;

    &__price {
        display: flex;
        flex-direction: column;
    }

    &__price-new {
        span {
            &:nth-child(1) {
                @media (max-width:$desktop) {
                    font-size: 18px;
                }
            }

            &:nth-child(2) {
                color: var(--primaryTwo);
                font-size: 15px;
                line-height: math.div(19, 15);
                font-weight: 400;

                @media (max-width:$desktop) {
                    font-size: 12px;
                }
            }
        }
    }

    &__price-old {
        margin-top: 5px;
        display: flex;
        align-items: center;
        color: var(--primaryTwo);

        span {
            &:nth-child(1) {
                color: var(--primaryTwo);
                font-size: 15px;
                line-height: math.div(19, 15);
                font-weight: 400;

                span {
                    position: relative;

                    &::before {
                        content: '';
                        border-bottom: 1px solid currentColor;
                        position: absolute;
                        width: 100%;
                        height: 50%;
                        transform: rotate(-5deg);
                    }
                }

                @media (max-width:$desktop) {
                    font-size: 12px;
                }
            }

            &:nth-child(2) {
                margin-left: 4px;
                padding: 2px 4px;
                color: var(--green);
                background: rgba(36, 202, 142, 0.1);
                border-radius: 8px;
                font-size: 12px;
                line-height: math.div(15, 12);
                font-weight: 400;
            }
        }
    }


    &__name {
        margin-top: 16px;

        font-size: 20px;
        line-height: math.div(25, 20);
        font-weight: 500;
        color: var(--dark);

        @media (max-width:$desktop) {
            font-size: 18px;
        }
    }

    &__address {
        margin-top: 5px;
        font-size: 15px;
        line-height: math.div(19, 15);

        &--one {
            display: block;
            font-weight: 500;
            color: var(--dark);
        }

        &--two {
            &:not(:first-child) {
                margin-top: 5px;
            }

            display: block;
            color: var(--primary600)
        }

        @media (max-width:$desktop) {
            font-size: 12px;
            margin-top: 4px;
        }
    }

    &__mortgage {
        margin-top: 2px;
        color: var(--primaryTwo);
        font-size: 15px;
        line-height: math.div(19, 15);
        font-weight: 400;

        span {
            color: var(--blue);
            font-weight: 500;
        }
    }

    &__text {
        margin-top: 32px;
        display: -webkit-box;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
        overflow: hidden;

        p {
            color: var(--dark);
            font-size: 15px;
            line-height: math.div(19, 15);
        }

        &._active {
            display: block;
        }

        @media (max-width:$desktop) {
            margin-top: 16px;
            -webkit-line-clamp: 3;
        }

        @media (max-width:$mobile-max) {
            -webkit-line-clamp: 6;
        }
    }

    &__more {
        display: flex;
        align-items: center;
        margin-top: 16px;
        color: var(--blue);

        span {
            font-size: 12px;
            line-height: 100%;
        }

        svg {
            margin-left: 5px;
            width: 20px;
            height: 20px;
            fill: currentColor;
        }

        @media (max-width:$desktop) {
            margin-top: 8px;

            svg {
                margin-left: 4px;
            }
        }
    }

    &__characteristics {
        margin-top: 48px;

        .object-characteristics {

            &__container {}

            &__list {
                @media (min-width:$smallTablet-min) {
                    grid-template-columns: 1.5fr 1fr;
                    grid-column-gap: 73px;
                }
            }

            &__item {
                span {
                    width: auto;

                    &:nth-child(2) {
                        width: 30%;
                    }
                }
            }

            &__more {}
        }

        padding: 0;
        box-shadow: none;
        background-color: transparent;
        border-radius: 0;

        @media (max-width:$smallTablet-max) {
            margin-top: 24px;
        }
    }

    .row {
        display: flex;
        align-items: center;

        @media (max-width:$smallTablet-max) {
            flex-direction: column;
            align-items: start;
        }
    }

    &__rating {
        margin-left: 32px;
        display: flex;
        align-items: center;

        >div {
            display: flex;
            align-items: center;

            svg {
                fill: var(--yellow);
                width: 16px;
                height: 16px;

                &:not(:last-child) {
                    margin-right: 4px;
                }
            }
        }

        >span {
            color: var(--primary600);
            font-size: 12px;
            line-height: math.div(15, 12);
            margin-left: 4px;

            &:nth-child(2) {
                font-weight: 500;
            }

            &:nth-child(3) {
                font-weight: 400;
            }
        }

        @media (max-width:$smallTablet-max) {
            margin: 4px 0 0 0;
        }
    }

    @media (max-width:$desktop) {
        padding: 16px;
    }
}

.object-banners {
    padding: 32px 24px;
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    border-radius: 12px;

    @media (max-width:$desktop) {
        padding: 16px;
    }
}



.object-history {
    padding: 32px 24px;
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    border-radius: 12px;

    &__title {}

    &__list {
        margin-top: 32px;
        display: flex;
        flex-direction: column;

        @media (max-width:$desktop) {
            margin-top: 16px;
        }

        @media (max-width:$smallTablet-max) {
            display: flex;
        }
    }

    &__item {
        display: grid;
        align-items: center;
        grid-template-columns: max-content 1fr 1fr;
        grid-column-gap: 100px;
        padding: 20px 0;

        &:first-child {
            padding-top: 0;
        }

        &:not(:last-child) {
            border-bottom: 1px solid var(--primary100);
        }

        @media (max-width:$smallTablet-max) {
            display: grid;
            grid-template-columns: max-content 1fr;
            grid-row-gap: 8px;
            grid-column-gap: 12px;
            padding: 12px 0;
        }
    }

    &__item--plus {
        .object-history {
            &__old-price {
                span {
                    &:nth-child(2) {
                        color: var(--red);
                        background: rgba(255, 72, 72, 0.1);
                    }
                }
            }
        }
    }

    &__item--minus {
        .object-history {
            &__old-price {
                span {
                    &:nth-child(2) {
                        color: var(--green);
                        background: rgba(36, 202, 142, 0.1);
                    }
                }
            }
        }
    }

    &__date {
        color: var(--dark);
        font-size: 15px;
        line-height: math.div(19, 15);

        @media (max-width:$smallTablet-max) {
            grid-column: 1/-1;

            &[data-mobile-text] {
                font-size: 0;

                &::before {
                    content: attr(data-mobile-text);
                    font-size: 12px;
                }
            }
        }
    }

    &__old-price {
        display: flex;
        align-items: center;
        color: var(--primaryTwo);

        span {
            &:nth-child(1) {
                color: var(--primaryTwo);
                font-size: 15px;
                line-height: math.div(19, 15);
                font-weight: 400;

                span {
                    position: relative;

                    &::before {
                        content: '';
                        border-bottom: 1px solid currentColor;
                        position: absolute;
                        width: 100%;
                        height: 50%;
                        transform: rotate(-5deg);
                    }
                }
            }

            &:nth-child(2) {
                margin-left: 8px;
                padding: 2px 4px;
                border-radius: 8px;
                font-size: 12px;
                line-height: math.div(15, 12);
                font-weight: 400;

                @media (max-width:$smallTablet-max) {
                    padding: 5px 4px;
                }
            }
        }
    }

    &__new-price {
        font-size: 15px;
        line-height: math.div(19, 15);
        color: var(--dark);

        @media (max-width:$smallTablet-max) {
            justify-self: end;
        }
    }

    @media (max-width:$desktop) {
        padding: 16px;
    }
}

.object-location {
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    border-radius: 12px;
    padding: 32px 24px;

    &__title {
        @media (max-width:$desktop) {
            padding: 0 16px;
        }
    }

    &__descr {
        margin-top: 20px;
        color: var(--primary600);
        font-size: 15px;
        line-height: math.div(19, 15);

        @media (max-width:$desktop) {
            margin-top: 16px;
            padding: 0 16px;
        }
    }

    &__maps {
        margin-top: 16px;
        width: 100%;
        height: 423px;
        background-color: var(--white);
        border-radius: 12px;

        @media (max-width:$desktop) {
            margin-top: 16px;
            height: 350px;
        }

        @media (max-width:$smallTablet-max) {
            height: 227px;
        }
    }

    @media (max-width:$desktop) {
        padding: 16px 0;
    }
}

// ================================================================================





.object-similar {
    background-color: var(--stand-bg);
    border-radius: 12px;
    padding: 32px;

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 32px;

        @media (max-width:$desktop) {
            margin-bottom: 24px;
        }

        @media (max-width:$smallTablet-max) {
            padding: 0 $containerOffset+px;
        }
    }

    &__title {}

    &__nav {
        display: flex;
        align-items: center;

        .nav-arrow-secondary {
            &:not(:last-child) {
                margin-right: 10px;
            }
        }

        @media (max-width:$smallTablet-max) {
            display: none;
        }
    }

    &__items {
        margin: -16px -6px;
        padding: 16px 6px;

        .swiper-wrapper {
            align-items: stretch;
        }

        .swiper-slide {
            height: auto;
        }

        .card-primary {
            height: 100%;
        }

        .card-secondary {
            height: 100%;
        }


    }

    @media (max-width:$smallTablet-max) {
        padding: 16px 0;
        background-color: var(--white);
    }
}



.object-two {
    .object-plate-bottom {
        &__btn {
            margin: 0;
        }
    }

    .bid-user--grade {

        &::before,
        &::after {
            height: 80px;
        }
    }

    .bid-user__grade {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 16px;
        border-top: none;
        padding-top: 0;
        margin-top: 32px;

        .bid-user {
            &__title {
                display: none;
            }

            &__btn--call {
                padding: 0 11px;
                margin-top: 0;
            }

            &__btn--message {
                padding: 0 16px;
                margin-top: 0;
            }

            &__btn--online-display {}

            &__btn--like {
                grid-column: 1/-1;
            }
        }
    }
}

.object-two-body {
    &__data {
        margin-top: 0;
    }
}

.object-calc-mort {
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    border-radius: 12px;
    padding: 24px;

    &__title {}

    &__wrapper {
        margin-top: 24px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 16px;
        grid-row-gap: 16px;

        @media (max-width:$desktop) {
            margin-top: 16px;
        }

        @media (max-width:$smallTablet-max) {
            grid-template-columns: 1fr;
        }
    }

    &__info {
        margin-top: 24px;

        color: var(--primaryTwo);
        font-size: 15px;
        line-height: math.div(19, 15);
        font-weight: 400;

        display: flex;
        align-items: center;
        justify-content: end;

        span {
            display: inline-block;
            margin-left: 16px;
            color: var(--dark);
            font-weight: 500;
        }

        @media (max-width:$desktop) {
            margin-top: 16px;
        }
    }

    &__bottom {
        margin-top: 32px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 16px;

        @media (max-width:$desktop) {
            margin-top: 24px;
        }

        @media (max-width:$smallTablet-max) {
            grid-template-columns: 1fr;
            grid-row-gap: 16px;
        }
    }

    &__app {
        svg {
            width: 20px;
            height: 20px;
            fill: var(--white);
            margin-left: 10px;
        }
    }

    &__message {
        svg {
            width: 20px;
            height: 20px;
            fill: var(--blue);
            margin-left: 10px;
            transition: fill $transitionSpeed $transitionFunction;
        }

        &:hover {
            svg {
                fill: var(--white);
            }
        }
    }

    @media (max-width:$desktop) {
        padding: 16px;
    }
}




.object-slider {
    display: flex;
    justify-content: space-between;
    max-height: 474px;

    &__body {
        overflow: hidden;
        margin-right: 5px;
        flex: 0 1 percent(702, 996);

        @media (max-width:$desktop) {
            margin-right: 0;
        }

        @media (max-width:$mobile-max) {
            flex: auto;
        }
    }

    &__nav {
        flex-shrink: 0;
        flex: 0 1 percent(289, 996);

        @media (max-width:$desktop) {
            display: none;
        }
    }

    @media (max-width:$desktop) {
        justify-content: center;
    }
}






.object-apart-renov {
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    padding: 32px 24px 24px;
    border-radius: 12px;

    &__title {
        margin-bottom: 16px;

        @media (max-width:$desktop) {
            padding: 0 $containerOffset+px;
        }
    }

    &__container {}

    &__tabs {
        .tabs-primary {
            &__container {
                margin-top: 24px;
                border-radius: 0;
                padding: 0;
            }

            &__inner {
                margin-top: 0;
                padding-top: 0;
            }

            &__content {
                margin-top: 0;
            }

            &__btns {
                .tabs__title {
                    width: auto;
                    flex-grow: 1;
                    font-weight: 400;

                    @media (max-width:$smallTablet-max) {
                        padding: 8px 16px;
                        flex-grow: 0;
                    }
                }
            }
        }

        @media (max-width:$desktop) {
            padding: 0;
            border-radius: 0;
        }
    }

    &__item {
        position: relative;
        height: 379px;

        @media (max-width:$desktop) {
            height: 420px;
        }

        @media (max-width:$smallTablet-max) {
            height: 385px;
        }
    }

    &__images {
        overflow: hidden;
        height: 100%;
        border-radius: 12px;
        width: 100%;
    }

    &__image {
        width: 100%;
        height: 100%;
        border-radius: 12px;
        object-fit: fill;
    }

    &__tags {
        position: absolute;
        z-index: 2;
        top: 15px;
        right: 15px;

        display: flex;
        align-items: center;
    }

    &__tag {
        font-size: 12px;
        line-height: math.div(15, 12);
        color: var(--blue);
        font-weight: 500;
        padding: 6.5px 8px;

        background-color: var(--white);
        backdrop-filter: blur(2px);
        border-radius: 8px;

        &:not(:last-child) {
            margin-right: 5px;
        }
    }

    &__mark {
        position: absolute;
        width: 30px;
        height: 30px;

        .secondary-tooltip {
            position: relative;

            &__btn {
                background: rgba(61, 61, 61, 0.7);
                border-radius: 50%;
                width: 30px;
                height: 30px;

                svg {
                    width: 14px;
                    height: 14px;
                    fill: var(--white);
                }
            }

            &__content {
                top: 0;
                left: 0;
                right: auto;
                bottom: auto;
                max-width: 168px;
                box-sizing: border-box;
                width: max-content;
                z-index: 3;

                @media (max-width:$smallTablet-max) {
                    transform: translate(0);
                    left: 0;
                }
            }
        }
    }

    &__nav {
        position: absolute;
        bottom: 12px;
        right: 12px;
        z-index: 2;
        display: flex;
        align-items: center;

        .nav-arrow-secondary {
            background-color: var(--white);
            border: 1px solid var(--white);

            svg {
                fill: #cbe2ff;
            }

            &:hover {
                border: 1px solid var(--blue);

                svg {
                    fill: var(--blue);
                }
            }
        }

        .nav-arrow-secondary--next {
            margin-left: 10px;
        }

        @media (max-width:$smallTablet-max) {
            display: none;
        }
    }

    @media (max-width:$desktop) {
        padding: 16px 0;
    }
}





.object-construct-progress {
    padding: 32px 24px;
    box-shadow: var(--shadow-primary);
    border-radius: 12px;
    background-color: var(--white);

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 24px;
    }

    &__nav {
        display: flex;
        align-items: center;

        .nav-arrow-secondary--next {
            margin-left: 10px;
        }

        @media (max-width:$smallTablet-max) {
            display: none;
        }
    }

    &__title {}

    &__select {
        width: 230px;
        margin-bottom: 24px;

        @media (max-width:$desktop) {
            width: 50%;
            margin-bottom: 16px;
        }

        @media (max-width:$mobile-max) {
            width: 100%;
        }
    }

    &__items {
        @media (max-width:$desktop) {
            padding: 0 16px;
            margin: 0 -16px;
        }
    }

    @media (max-width:$desktop) {
        padding: 16px;
    }
}



























.object-slider-body {
    position: relative;

    &__wrapper {
        overflow: hidden;
        height: 100%;
        border-radius: 12px;
        width: 100%;

        .swiper-slide {
            a {
                display: block;
                width: 100%;
                height: 100%;
            }

            img {
                width: 100%;
                height: 100%;
                border-radius: 12px;
            }
        }
    }

    &__navigation {
        @media (max-width:$smallTablet-max) {
            display: none;
        }
    }

    @media (max-width:$desktop) {
        height: 400px;
    }

    @media (max-width:$smallTablet-max) {
        height: 285px;
    }
}

.object-slider-nav {
    overflow: hidden;

    &__wrapper {
        width: 100%;
        height: 100%;
        overflow: hidden;
        padding: 15px;
        margin: -15px;
        box-sizing: content-box;

        .swiper-slide {
            cursor: pointer;
            filter: drop-shadow(0px 15px 15px rgba(0, 0, 0, 0.04));

            a {
                display: block;
                width: 100%;
                height: 100%;
            }

            img {
                width: 100%;
                height: 100%;
                border-radius: 12px;
            }
        }
    }
}
